<template>
    <div class="swiper-box">
        <!-- swiperOption 自动轮播 -->
        <swiper :options="swiperOption">
            <swiper-slide v-for="(v,i) of swiperList" :key=" 'swiper'+i ">
                <img :src="v.image_800">
            </swiper-slide>
            <!-- 轮播图下面的小圆点 -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
  data() {
    return {
      swiperOption: {
        // autoplay: true, //可选选项，自动滑动
        autoplay:{
            disableOnInteraction:false,
            delay:2000
        },
        pagination: {
          el: ".swiper-pagination"
        }
      }
    };
  },
//   获取父组件传递的数据 数组
  props: {
    swiperList: { type: Array, default: [] }
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.swiper {
  &-box {
    height: 310px;
    overflow-y: hidden;
    img {
      width: 100%;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: 82px;
      left: -290PX; 
      width: 100%;
    }
    .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet{
      margin:0 10px;
    }
    .swiper-pagination-bullet {    
      width: 12px;
      height: 12px;
      display: inline-block;
      border-radius: 100%;
      background: white;
      opacity: 0.6;
    }
    .swiper-pagination-bullet-active {
      opacity: 1;
      background: #007aff;
    }
  }
}
</style>


