<template>
    <div> 
        <NarBarSearch class="topsae"></NarBarSearch>
        <scroll-view class="search-wrapper">
            <div class="searchpage">        
                <div class="searchtitle">
                    <div v-for="(v,i) of search.sortFilter" :key="'sortFilter'+i">
                        <div @click="sendseatype(i,v.sortKey)" :class="{seasort:isactive == i}" >{{ v.title }}</div>
                    </div>
                </div>
                <div class="searchbottom">
                    <div v-for="(v, i) of search.searchlist" :key="'products'+i">
                        <div class="guessimg" :style="`background-image:url(${v.img})`"></div>
                        <div class="vtitle">{{v.title }}</div>
                        <div>
                            <div>&#65509;{{ v.price }}</div>
                            <div>{{ v.cfav }} <i class="iconfont icon-shoucang"></i></div>
                        </div>
                    </div>
                </div> 
            </div> 
        </scroll-view>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import NarBarSearch from "../components/NarBarSearch";
export default {
  data() {
    return {
      isactive: 0
    };
  },
  components: {
    NarBarSearch
  },
  computed: {
    ...mapState(["search"])
  },
  methods: {
    ...mapActions(["loadSearchListPage"]),
    sendseatype(i,sort) {
      this.loadSearchListPage({
        q:this.$route.query.p,
        page: "1",
        sort: sort,
        cKey: "46"
      });
      this.isactive = i;
    }
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.topsae{
  z-index: 100;
}
.seasort {
  color: #ff5577;
}
.search-wrapper {
  position: absolute;
  top: 89px; /*px*/
  left: 0px;
  right: 0px;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  >.searchpage{
    height:100%;
  }
}
.searchtitle {
  padding: 25px 0;
  border-bottom:1px solid #ccc;
  display: flex;
  width: 100%;
  height: 32px;
  :not(:first-of-type) {
    border-left: 1px solid #ccc;
  }
  > div {
    font-family: 黑体;
    text-align: center;
    line-height: 30px;
    height: 30px;
    font-size: 27px;
    width: 33%;
  }
}
.searchbottom {
  border-top: 1px solid #ccc;
  padding-top: 15px;
  width: 100%;
  flex-flow: row wrap;
  display: flex;
  overflow: hidden;
  justify-content: center;
  > div {
    margin: 0 12px;
    width: 45%;
    height: 520px;
    .guessimg {
      height: 380px;
      border-radius: 5px;
      background-size: cover;
      background-position: center;
    }
    .vtitle {
      margin: 10px 0;
      display: -webkit-box;
      //超出部分省略号
      -webkit-box-orient: vertical;
      // 两行显示
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-indent: 16px;
      color: #333;
      font-family: 微软雅黑;
    }
    > div:last-child {
      line-height: 40px;
      padding-right: 20px;
      padding-left: 6px;
      display: flex;

      justify-content: space-between;
      > div:first-child {
        font-size: 30px;
        font-weight: 600;
        color: #333;
      }
      > div:last-child {
        color: #666;
        font-size: 25px;
      }
    }
  }
}
</style>



