<template>
    <div class="mepage">
        <div>
            <div><img src="../../static/images/tx.jpg" alt=""></div>
            <div>
                <div class="idme">Astraeus</div>
                <div class="adress">我的收货地址 <span>&gt;</span></div>
            </div>
        </div>
        <div><img src="../../static/images/Me (9).png" alt=""> 我的订单 <span>&gt;</span></div>
        <div>
            <div><img src="../../static/images/Me (1).png" alt=""><div>我的购物车</div><span>&gt;</span></div>
            <!-- <div><img src="../../static/images/Me (2).png" alt=""><div>我的拼团</div><span>&gt;</span></div> -->
            <!-- <div><img src="../../static/images/Me (3).png" alt=""><div>我的钱包<
                span>快速提现</span></div><span>&gt;</span></div> -->
            <div><img src="../../static/images/Me (10).png" alt=""><div>我的优惠券</div><span>&gt;</span></div>
            <div><img src="../../static/images/Me (6).png" alt=""><div>我收藏的商品</div><span>&gt;</span></div>
            <div><img src="../../static/images/Me (5).png" alt=""><div>我收藏的店铺</div><span>&gt;</span></div>
        </div>
        <div>
            <div><img src="../../static/images/Me (8).png" alt=""><div>通知</div><span>&gt;</span></div>
            <div><img src="../../static/images/Me (4).png" alt=""><div>客服</div><span>&gt;</span></div>
            <div><img src="../../static/images/Me (7).png" alt=""><div>意见反馈</div><span>&gt;</span></div>
        </div>
        <div></div>
       <BottomBar></BottomBar>
    </div>
    
</template>

<script>
import BottomBar from "../components/BottomBar";

export default {
  components: {
    BottomBar
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.mepage {
  background: #f1f1f1;
  > div:first-child {
    display: flex;
    padding: 40px;
    background: #fdfdfd;
    .idme {
      font-size: 35px;
    }
    .adress {
      font-family: 微软雅黑;
      margin-top: 3px;
      font-size: 23px;
      > span {
        color: #ccc;
        font-weight: 600;
        font-size: 30px;
        margin-left: 50px;
      }
    }
    > div:first-child {
      margin-right: 36px;
      img {
        width: 100px;
        height: 100px;
        border-radius: 50px;
      }
    }
  }
  > div:nth-child(2) {
    margin: 18px 0;
    border-bottom: 1px solid #bbb;
    background: #fdfdfd;
    padding: 22px 20px 22px 40px;
    display: flex;
    height: 55px;
    line-height: 55px;
    font-size: 30px;
    font-family: 微软雅黑;
    > img {
      margin-right: 36px;
    }
    > span {
      color: #ccc;
      font-weight: 600;
      font-size: 31px;
      margin-left: 450px;
    }
  }
  > div:nth-child(3),div:nth-child(4) {
    background: #fdfdfd;
    margin-top: 20px;
    padding: 10px 0;
    > div {
      padding: 19px 20px 19px 40px;
      display: flex;
      height: 56px;
      line-height: 56px;
      > div {
        font-size: 31px;
        font-family: 微软雅黑;
        margin-left: 36px;
        border-bottom: 1px solid #aaa;
      }
      >div:not(:first-of-type){
          >div{
            border-bottom: 1px solid #aaa;
          }
      }
    }
  }
}
</style>


