<template>
    <div class="navbar-search">
        <div class="search">
            <div> </div>
            <i v-show="flag" class="iconfont icon-xiangzuojiantou" id="xzjt" @click="flag=false"></i>
            <input v-show="!flag" class="user-input" type="text" @focus="flag=true" :value="userInput">
            <input v-show="flag" class="user2-input" type="text" @focus="flag=true" v-model="userInput">
            <i v-show="!flag" class="iconfont icon-gouwuche"></i>
            <span v-show="flag" @click="sendsearch()" class="spansear" >搜索</span>
        </div>
        <!-- <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div v-show="flag" class="search-content">
                <div><i class="iconfont icon-sousuo"></i> 历史搜索<span class="iconfont icon-lajixiang"></span></div> 

                <div class="lastinfont"><i class="iconfont icon-shoucang"></i> 热门搜索</div>
            </div>
        </transition> -->
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      flag: false,
      userInput: "",
      wordplace: "",
    };
  },
  computed: {
    ...mapState(["search"])
  },
  mounted() {
    //不需要初始化
  },
  methods: {
    ...mapActions(["loadSearchListPage"]),
    sendsearch() {
      this.loadSearchListPage({
        q: this.userInput,
        page: "1",
        sort: "pop",
        cKey: "46"
      });
      
      console.log(this.userInput);
      this.wordplace = this.userInput;
      this.$router.push({
        path:"/search",query: {p:this.userInput }
      })
    }
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.navbar-search {
  z-index: 100;
  > .search {
    border-bottom: 1px solid #ccc;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    background-color: @white;
    height: 88px;
    /* 弹性盒子 */
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    :first-child {
      width: 30px;
    }
    > i {
      color: @gray;
      font-weight: 550;
      font-size: 45px;
      line-height: 90px;
      flex-grow: 1;
      text-align: center;
    }
    > #xzjt {
      font-size: 38px;
      margin-left: -20px;
    }
    .spansear {
      color: @gray;
      font-size: 28px;
      line-height: 90px;
      flex-grow: 1;
      text-align: center;
      margin-right: 16px;
    }
    > .user-input {
      flex-grow: 5;
      margin: 15px 0px;
      border: 1px solid #eee;
      border-radius: 5px;
      background: #eee;
      margin-right: 5px;
      font-size: 28px;
    }
    > .user2-input {
      font-size: 30px;
      padding-left: 10px;
      flex-grow: 6;
      margin: 19.5px 0px;
      border: 1.5px solid #ff5777;
      border-radius: 10px;
    }
    > .user2-input:hover {
      outline: none;
      border: 1.5px solid #ff5777;
      box-sizing: border-box;
    }
  }
  .search-content {
    z-index: 10;
    /* display: none; */
    position: absolute;
    top: 89px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: @white;
    > div {
      padding-left: 30px;
      margin-top: 10px;
    }
    :first-child {
      font-size: 26.8px;
      font-family: 微软雅黑;
      color: #999;
      margin-top: 30px;
      line-height: 40px;
      > i {
        font-size: 31px;
      }
      > span {
        font-size: 31.5px;
        margin-left: 510px;
      }
    }
    > .lastinfont {
      padding-top: 22px;
      border-top: 1px solid #ddd;
      font-size: 26.8px;
      font-family: 微软雅黑;
      color: #999;
      margin-top: 30px;
      line-height: 40px;
      > i {
        font-size: 31px;
      }
      > span {
        font-size: 31.5px;
        margin-left: 510px;
      }
    }
  }
}
</style>


