<template>
   <div>
       <div class="title"><span>———— ◆</span>&nbsp;&nbsp;猜你喜欢&nbsp;&nbsp;<span>◆ ————</span></div>
       <div class="guesslist">
           <div v-for="(v,i) of guessList" :key="'guessList'+i">
               <div class="guessimg" :style="`background-image:url(${v.img})`"></div>
               <div class="vtitle">{{ v.title }}</div>
               <div>
                   <div>&#65509;{{ v.price }}</div>
                   <div>{{ v.cfav }} <i class="iconfont icon-shoucang"></i></div>
               </div>
           </div>
       </div>
   </div>
</template>

<script>
export default{
   props : {
       guessList: {type:Array, default:[]}
   }
}
</script>

<style lang="less">
@import url("../less/less-vue");
.title{
    margin-top:20px;
    margin-bottom: 15px;
    font-size: 30px;
    text-align: center;
    >span{
        font-size: 28px;
    }
}
.guesslist{
    margin-top:30px;
    width: 100%;
    // text-align: center;
    flex-flow: row wrap;
    display: flex;
    overflow: hidden;
    justify-content: center;
    >div{
        margin:0 12px;
        width:45%;
        height:620px;
        .guessimg{
            height: 460px;
            border-radius: 15px;
            background-size: cover;
            background-position: center;
        }
        .vtitle{
            margin:10px 0;
            display: -webkit-box;
            //超出部分省略号
            -webkit-box-orient: vertical;
            // 两行显示
            -webkit-line-clamp: 2;
            overflow: hidden;
            text-indent:16px;
            color:#333;
            font-family: 微软雅黑;
        }
        >div:last-child{
            line-height: 40px;
            padding-right: 20px;
            padding-left: 6px;
            display: flex;
            
            justify-content:space-between;
            >div:first-child{
                font-size: 30px;
                font-weight: 600;
                color:#333;
            }
            >div:last-child{
                color:#666;
                font-size: 25px;
            }
        }


    }
    
}

</style>


