<template>
    <div> 
        <router-link :to="'/detailpage'" tag="div">
            <div class="rightList">
                <div v-for="(item, i) of type.rightList" :key="'rightList'+i">
                    <img :src="item.image">
                    <div>{{ item.title }}</div>
                </div>
            </div> 
        </router-link>
        <div class="listtitle">
            <div v-for="(v,i) of type.sortFilter" :key="'sortFilter'+i">
                <div @click="sendtype(i,v.sortKey)" :class="{sort:isactive == i}">{{ v.title }}</div>
            </div>
        </div>
        <div class="rightbottom">
            <div v-for="(v, i) of type.products" :key="'products'+i">
                <div class="guessimg" :style="`background-image:url(${v.img})`"></div>
                <div class="vtitle">{{v.title }}</div>
                <div>
                    <div>&#65509;{{ v.price }}</div>
                    <div>{{ v.cfav }} <i class="iconfont icon-shoucang"></i></div>
                </div>
            </div>
        </div> 
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      isactive: 0
    };
  },
  computed: {
    ...mapState(["type"])
  },
  mounted() {
    //初始化右边加载
    this.loadTypesRightPage();
    this.loadTypesRightListPage({});
  },
  methods: {
    ...mapActions(["loadTypesRightPage", "loadTypesRightListPage"]),
    sendtype(i, sort) {
      this.loadTypesRightListPage({
        fcid: this.type.fcid,
        sort: sort
      });
      this.isactive = i;
    }
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.sort {
  color: #ff5577;
}
.rightbottom {
  border-top: 1px solid #ccc;
  padding-top: 15px;
  width: 100%;
  // text-align: center;
  flex-flow: row wrap;
  display: flex;
  overflow: hidden;
  justify-content: center;
  > div {
    margin: 0 12px;
    width: 45%;
    height: 520px;
    .guessimg {
      height: 380px;
      border-radius: 5px;
      background-size: cover;
      background-position: center;
    }
    .vtitle {
      margin: 10px 0;
      display: -webkit-box;
      //超出部分省略号
      -webkit-box-orient: vertical;
      // 两行显示
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-indent: 16px;
      color: #333;
      font-family: 微软雅黑;
    }
    > div:last-child {
      line-height: 40px;
      padding-right: 20px;
      padding-left: 6px;
      display: flex;

      justify-content: space-between;
      > div:first-child {
        font-size: 30px;
        font-weight: 600;
        color: #333;
      }
      > div:last-child {
        color: #666;
        font-size: 25px;
      }
    }
  }
}
.listtitle {
  margin: 0 20px;
  padding: 25px 0;
  border-top: 1px solid #ccc;
  //   border-bottom:1px solid #ccc;
  display: flex;
  width: 100%;
  height: 32px;
  :not(:first-of-type) {
    border-left: 1px solid #ccc;
  }
  > div {
    font-family: 黑体;
    text-align: center;
    line-height: 30px;
    height: 30px;
    font-size: 27px;
    width: 33%;
  }
}
.rightList {
  padding-top: 30px;
  width: 100%;
  //换行
  flex-wrap: wrap;
  display: flex;
  overflow: hidden;
  > div {
    width: 33%;
    text-align: center;
    height: 200px;
    //   line-height: 200px;
    > img {
      width: 70%;
    }
  }
}
</style>


