import api from '../../server/api';

const state = {
    leftList: [],
    rightList: [],
    fcid:"",
    sortFilter:[],
    products:[]
}



const actions = {
    loadTypesPage({ commit }) {
        api.loadDataByPids([117330], (data) => {
            commit("setleftList", data.data["117330"].list)
        })
    },
    loadTypesRightPage({commit},pid=3627){
        let params = {
            appPlat:"m",
            pid
        }
        api.loadDataRightTypes(params,(data)=>{
            commit("setrightList",data.data.list)
        })
    },
    loadTypesRightListPage({commit},{fcid="10062603",sort="pop"}){
        let params = {
            fcid,
            page:"1",
            sort,
            cKey:"h5-cube"
        }
        api.loadDataRightProduct(params,(data)=>{
            commit("setfcid",data.result.param.fcid);
            commit("setsortFilter",data.result.sortFilter);    
            commit("setproducts",data.result.wall.docs);      
            // console.log(data.result.param.fcid);
            // console.log(data.result.sortFilter);
            // console.log(data.result.wall.docs);
        })
    }
}

const mutations = {
    setleftList(state, data) {
        state.leftList = data;
    },
    setrightList(state,data){
        state.rightList = data;
    },
    setfcid(state, data){
        state.fcid = data;
    },
    setsortFilter(state, data){
        state.sortFilter = data;
    },
    setproducts(state, data){
        state.products = data;
    }
}

export default {
    state,
    actions,
    mutations
}


