import api from '../../server/api';

const state = {
    swiperList: [],
    resList: [],
    lazyList: [],
    stickyList: [],
    guessList: [],
}



const actions = {
    loadHomeDatas({ commit }) {
        api.loadDataByPids([122224, 106930, 51833, 51836,], (data) => {
            commit("setswiperList", data.data["122224"].list)
            commit("setresList", data.data["106930"].list)
            commit("setlazyList", data.data["51833"].list)
            commit("setstickyList", data.data["51836"].list)
        }),
        api.loadDataGuessLikes("9750", (data) => {
            commit("setguessList", data.result.wall.docs)
        })
    }
}

const mutations = {
    setswiperList(state, data) {
        state.swiperList = data;
    },
    setresList(state, data) {
        state.resList = data;
    },
    setlazyList(state, data) {
        state.lazyList = data;
    },
    setstickyList(state, data) {
        state.stickyList = data;
    },
    setguessList(state, data) {
        state.guessList = data;
    },
}

export default {
    state,
    actions,
    mutations
}


