<template>
     <div class="type-left-box">
       <div v-for="(v,i) of leftList" :key="'leftList'+i" :class="{gaoliang:isactive == i}">
           <div @click="sendtype(i,v.maitKey,v.miniWallkey)">{{ v.title }}</div>
       </div>
   </div>
</template>

<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default{
    data(){
        return {
            isactive:0
        }
    },
    props: {
        leftList: { type:Array,default: []}
    },
    methods:{
        ...mapActions(["loadTypesRightPage","loadTypesRightListPage"]),
        sendtype(i,pid,miniWallkey){
            this.loadTypesRightPage(pid);
            this.loadTypesRightListPage({fcid:miniWallkey,sort:"pop"});
            this.isactive = i;
        }
    }
}
</script>

<style lang="less">
@import url("../less/less-vue");
.gaoliang{
    font-weight: bold;
    background: @white;
    div{
        color:#ff5577;
        border-left:8px solid #ff5577!important;
      }
    }
.type-left-box{
    >div{
        text-align: center;
        padding: 30px 0;
        font-size: 26px;
        color: #444;
        font-family: 微软雅黑;
        >div{
            height:30px;
            border-left:8px solid transparent;
        }
    }
}
</style>


