import api from '../../server/api'


const state = {
     searchlist:[],
     sortFilter:[],

}

const actions = {
    loadSearchListPage({commit},e){
        api.loadDataRightProduct(e,(data)=>{
            commit("setsearchlist",data.result.wall.docs);
            commit("setsortFilter",data.result.sortFilter);
        })
    }
}

const mutations = {    
    setsearchlist(state, data){
        state.searchlist = data;
    },
    setsortFilter(state, data){
        state.sortFilter = data;
    }
}

export default {
    state,
    actions,
    mutations
}




