import jsonpUtil from "../util/jsonpUtil"


// 请求地址
const mce_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_search = "https://list.mogujie.com/search";
const right_type = "https://mce.mogucdn.com/jsonp/get/3";


//参数常量
const pidsParams = {
    appPlat:"m"
}
const guesspid = {
    sort:"pop",
    cKey:46,
}

const error = function(error){
    alert(error.message);
}

//请求方法
export default {
    loadDataByPids(pids,success=()=>{}){
        if(!pids){
            throw new Error("请求参数错误");
        }
        if(!Array.isArray(pids)){
            pidsParams.pids = pids;
        }
        if(Array.isArray(pids)){
            let temp = "";
            for (const i in pids) {
                temp += pids[i]+",";
            }
            pidsParams.pids = temp.replace(/,$/,"");
        }
         jsonpUtil.request(mce_multiget,pidsParams)
                  .then((response)=>{
                      if(response.ok){
                          return response.json();
                      }else{
                          throw new Error("请求失败");
                      }
                  })
                  .then(success)
                  .catch(error)
    },
    loadDataGuessLikes(pid,success=()=>{}){
         guesspid.pid = pid;
         jsonpUtil.request(list_search,guesspid)
                  .then((response)=>{
                      if(response.ok){
                          return response.json();
                      }else{
                          throw new Error("请求失败");
                      }
                  })
                  .then(success)
                  .catch(error)
    },
    loadDataRightTypes(params,success=()=>{}){
        jsonpUtil.request(right_type,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)

    },
    loadDataRightProduct(params,success=()=>{}){
        // console.log(params)
        jsonpUtil.request(list_search,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)

    }
}


