<template>
    <div class="reslist">
        <div v-for="(item, i) of resList" :key="'resList'+i">
            <img :src="item.image">
            <div>{{ item.title }}</div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    resList: { typeof: Array, default: [] }
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.reslist {
  margin-top: 36px;
  width: 100%;
  //换行
  flex-wrap: wrap;
  display: flex;
  overflow: hidden;
  > div {
    margin-bottom: 36px;
    text-align: center;
    width: 150px;
    img {
      width: 70%;
    }
    div {
      margin-top: 10px;
      color: #444;
      font-weight: 100;
      font-family: 宋体;
    }
  }
}
</style>


