<template>
    <!-- 滚轮better-scroll -->
    <div>
        <NavBar></NavBar>
        <scroll-view class="wrapper">
            <!-- :swiperList="swiperList" 绑定 向子组件传递数据 -->
            <SwiPers :swiperList="home.swiperList"></SwiPers>
            <ResWrap :resList="home.resList"></ResWrap>
            <LazyData :lazyList="home.lazyList"></LazyData>
            <SpecialSticky :stickyList="home.stickyList"></SpecialSticky>
            <GuessLikes :guessList="home.guessList"></GuessLikes>
        </scroll-view>
        <BottomBar></BottomBar>
   </div>  
</template>

<script>
import NavBar from "../components/NavBar";
import SwiPers from "../components/SwiPers";
import ResWrap from "../components/ResWrap";
import LazyData from "../components/LazyData";
import SpecialSticky from "../components/SpecialSticky";
import GuessLikes from "../components/GuessLikes";
import BottomBar from "../components/BottomBar";
// import BScroll from 'better-scroll'
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  components: {
    NavBar,
    SwiPers,
    ResWrap,
    LazyData,
    SpecialSticky,
    GuessLikes,
    BottomBar
    //    SwiPers
  },
  //    data() {
  //     return {
  //       swiperList: [],
  //       resList: [],
  //       lazyList: [],
  //       stickyList: [],
  //       guessList: [],
  //     };
  //   },
  //   methods: {
  //       loadHomeDatas() {
  //           api.loadDataByPids([122224,106930,51833,51836],(data)=>{
  //             //   console.log(data);
  //               this.swiperList = data.data["122224"].list;
  //               this.resList = data.data["106930"].list;
  //               this.lazyList = data.data["51833"].list;
  //               this.stickyList = data.data["51836"].list;
  //           }),
  //           api.loadDataGuessLikes("9750",(data)=>{
  //             //   console.log(data);
  //               this.guessList = data.result.wall.docs;
  //           })
  //       },
  //   },
  computed: {
    ...mapState(["home"])
  },
  methods: {
    // this.loadHomeDatas()
    ...mapActions(["loadHomeDatas"])
  },
  mounted() {
    this.loadHomeDatas();
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.wrapper {
  position: absolute;
  top: 89px; /*px*/
  bottom: 99px; /*px*/
  left: 0px;
  right: 0px;
  // height: 400px;
  background-color: @white;
  overflow: hidden;
}
</style>


