<template>
    <div>
        <div class="title"><span>———— ◆</span>&nbsp;&nbsp;热门分类&nbsp;&nbsp;<span>◆ ————</span></div>
        <div class="stickyList">
            
            <div v-for="(item, i) of stickyList" :key="'stickyList'+i">
                <img :src="item.image">
                <div>{{ item.title }}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default{
   props: {
       stickyList: { type:Array, default: [] }
   }
}
</script>

<style lang="less">
@import url("../less/less-vue");
 .title{
    margin-top:20px;
    margin-bottom: 15px;
    font-size: 30px;
    text-align: center;
    >span{
        font-size: 28px;
    }
 }
 .stickyList{
    margin-top:36px;
    width: 100%;
    text-align: center;
    //换行
    flex-wrap: wrap;
    display: flex;
    overflow: hidden;
    >div{
        margin-bottom: 36px;
        
        width:185px;
        img{
            width:70%;
        }
        div{
            margin-top:10px;
            color:#444;
            font-weight: 100;
            font-family: 宋体;
        }
    }
  }
</style>


