<template>
    <div class="types-box">
        <NavBar></NavBar>
        <div class="wrapper">
            <scroll-view class="left-wrapper">
                <LeftTypes :leftList="type.leftList"></LeftTypes>
            </scroll-view>
            <scroll-view class="right-wrapper">
                <RightTypes></RightTypes>
            </scroll-view>
        </div>
        <BottomBar></BottomBar>
    </div>  
</template>

<script>
import NavBar from "../components/NavBar";
import LeftTypes from "../components/LeftTypes";
import RightTypes from '../components/RightTypes'
import BottomBar from "../components/BottomBar";

import { mapState, mapActions, mapMutations } from "vuex";

export default {
  components: {
    NavBar,
    LeftTypes,
    RightTypes,
    BottomBar
  },
  computed: {
    ...mapState(["type"])
  },
  methods: {
    // this.loadHomeDatas()
    ...mapActions(["loadTypesPage"])
  },
  mounted() {
    this.loadTypesPage();
  }
};
</script>

<style lang="less">
@import url("../less/less-vue");
.types-box {
  > .wrapper {
    position: absolute;
    top: 89px; /*px*/
    bottom: 99px; /*px*/
    left: 0px;
    right: 0px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    > .left-wrapper {
      width: 25%;
      background-color: #f6f6f6;
    }
    > .right-wrapper {
      width: 75%;
      background-color: #fff;
    }
  }
}
</style>


