<template>
   <div class="lazylist">
       <div class="title"><span>———— ◆</span>&nbsp;&nbsp;超实惠-促销直达&nbsp;&nbsp;<span>◆ ————</span></div>
       <div v-for="(v,i) of lazyList" :key="'lazyList'+i" :class="'lazyList'+i">
           <div class="ttitle">
               <div class="vtitle">{{ v.title }}</div>
               <div class="vicetitle">{{ v.viceTitle  }}</div>
            </div>
            <img :src="v.image">
       </div>
   </div>
</template>

<script>
export default{
   props: {
       lazyList: { typeof:Array, default: [] }
   }
}
</script>

<style lang="less">
@import url("../less/less-vue");
.title{
    margin-bottom: 15px;
    font-size: 30px;
    text-align: center;
    >span{
        font-size: 28px;
    }
}
.lazylist{
    font-family: 微软雅黑;
    width:100%;
    // display: flex;
    // flex-wrap: wrap;
    // overflow: hidden;
    .vtitle{
        font-size: 31px;
        color:#333;
        font-weight: 700;
    }
    .vicetitle{
        font-size: 25px;
        color:rgb(214, 49, 76);
    }
    .lazyList0{
        text-align: center;
        height: 300px;
        width: 50%;
        border-right:1px solid #bbb;
        border-bottom: 1px solid #bbb;
        border-top:1px solid #bbb;
        float: left;
         .ttitle{
            margin-top: 23px;
        }
        >img{
            margin-top:5px; 
            height:65%;
            margin-left:20px;
        }
    }
    .lazyList1{
        display: flex;
        width:50%-0.3;
        height:120px;
        padding-top: 30px;
        border-top:1px solid #bbb;
        border-bottom: 1px solid #bbb;
        float: left;
        .ttitle{
            margin-left: 30px;
        }
        >img{
            margin-top:-25px; 
            height:120%;
            margin-left:20px;
        }
    }
    .lazyList2{
        display: flex;
        width: 50%-0.3;
        float: left;
        height:120px;
        padding-top: 30px;
        border-bottom: 1px solid #bbb;
         .ttitle{
            margin-left: 30px;
        }
         >img{
            margin-top:-20px; 
            height:110%;
            margin-left:10px;
        }
    }
    .lazyList3,.lazyList4{
         border-right: 1px solid #bbb;
    }
    .lazyList3,.lazyList4,.lazyList5{
        text-align: center;
        display: inline-block;
        width:33%;
        height:250px;
        border-bottom: 1px solid #bbb;
        padding-top: 15px;
        >img{
            height: 70%;
        }
    }
}
</style>


